import React from "react"
import axios from "axios"
import {
    Table,
    Tbody,
    Tr,
    Td,
    TableCaption,
    Box,
    Text,
    Popover,
    PopoverTrigger,
    Button,
    PopoverContent,
    PopoverHeader,
    PopoverArrow,
    PopoverCloseButton,
    PopoverBody
  } from "@chakra-ui/react"



class Card extends React.Component{
    state={hidden: false, size: 0, lang:""}    

    renderPosts = async() => {
        try {
            const config = {
                headers: { Authorization: `Bearer f85a42d5a01a9493a95b25d92ed2c5553a9209d3` }
            };
            let res =await axios.get(this.props.repos_url)
            let sizeSave = 0;

          let posts = res.data;
          this.setState({size: posts.map((post, i) => (post.size+= post.size))
          });
          this.setState({size: this.state.size.reduce((a,b) => a + b,0)})
          console.log(this.state.size)
          this.setState({lang: posts.map((repo) => (repo.language)).reduce(
            (a,b,i,arr)=>
             (arr.filter(v=>v===a).length>=arr.filter(v=>v===b).length?a:b),
            null)})
            console.log(this.state.lang)
        } catch (err) {
          console.log(err);
        }
      }

    render(){
        const profile = this.props; 

        return(
            <Box hidden={this.state.hidden} className="col-md-6 col-lg-4" style={{width: "18rem", marginTop:150, marginBottom: 20}}>
                <span onClick={() => {this.setState({hidden: true})}} className="dot" style={{marginBottom: 10}}></span>
                <a rel="noreferrer" target="_blank" href={profile.html_url}>
                    <img className="card-img-top" src={profile.avatar_url} alt=""/>
                </a>                
                <div className="card-body">
                    <h5 className="card-title"><Text fontSize="2xl">{profile.name}</Text></h5>
                    <p className="card-text"><Text color="gray.500">{profile.bio}</Text></p>
                    <Popover placement="top-start">
                        <PopoverTrigger>
                            <Button
                            onClick={this.renderPosts}
                            p={4}
                            color="white"
                            fontWeight="bold"
                            borderRadius="md"
                            bgGradient="linear(to-l, #8d769c,#b06cba)" 
                            _hover={{
                                bgGradient:"linear(to-l, #7928CA,#FF0080)",
                            }} mt={4} >More details</Button>
                        </PopoverTrigger>
                        <PopoverContent>
                            <PopoverHeader fontWeight="semibold">Additional details</PopoverHeader>
                                <PopoverArrow />
                            <PopoverCloseButton />
                            <PopoverBody>
                            <Tbody>
                        <Tr>
                            <Td ><Text as="i">Total size of repos in KB:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em"> {this.state.size}kb</Text></Td>
                        </Tr>
                        <Tr>
                            <Td ><Text as="i">Total size of repos in MB:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{this.state.size / 1000}mb</Text></Td>
                        </Tr>
                        <Tr>
                            <Td ><Text as="i">Most used language:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{this.state.lang}</Text></Td>
                        </Tr>  
                        <Tr>
                            <Td ><Text as="i">Account last update:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{profile.updated_at.split("T")[0]}</Text></Td>
                        </Tr>                      
                    </Tbody>  
                            </PopoverBody>
                        </PopoverContent>
                    </Popover>                
                </div>
                <Table>
                    <Tbody>
                        <Tr>
                            <Td ><Text as="i">Public repos:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{profile.public_repos}</Text></Td>
                        </Tr>
                        <Tr>
                            <Td ><Text as="i">Followers:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{profile.followers}</Text></Td>
                        </Tr>
                        <Tr>
                            <Td ><Text as="i">Following:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{profile.following}</Text></Td>
                        </Tr>  
                        <Tr>
                            <Td ><Text as="i">Created:</Text> <Text bgGradient="linear(to-l, #7928CA,#FF0080)"bgClip="text"as="em">{profile.created_at.split("T")[0]}</Text></Td>
                        </Tr>                      
                    </Tbody>                    
                </Table>
            </Box>
        )
    }
}

export default Card;